
"use client";

import { useState, useRef } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { ChevronLeft, Upload, Loader2, Image as ImageIcon, FileText } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/hooks/use-toast";
import { supabase } from "@/lib/supabase";
import Image from "next/image";

export default function EbookAddPage() {
  const { toast } = useToast();
  const router = useRouter();
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [price, setPrice] = useState(0);
  const [description, setDescription] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [coverImageFile, setCoverImageFile] = useState<File | null>(null);
  const [ebookFile, setEbookFile] = useState<File | null>(null);

  const coverFileInputRef = useRef<HTMLInputElement>(null);
  const ebookFileInputRef = useRef<HTMLInputElement>(null);


  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!coverImageFile || !ebookFile) {
        toast({
            title: "Missing Files",
            description: "Please upload both a cover image and an ebook PDF.",
            variant: "destructive",
        });
        return;
    }
    setIsLoading(true);

    try {
        // 1. Upload Cover Image
        const coverFileName = `${Date.now()}-${coverImageFile.name}`;
        const { data: coverUploadData, error: coverUploadError } = await supabase.storage
            .from('ebook-covers')
            .upload(coverFileName, coverImageFile);
        
        if (coverUploadError) throw coverUploadError;
        
        const { data: { publicUrl: coverImageUrl } } = supabase.storage
            .from('ebook-covers')
            .getPublicUrl(coverFileName);

        // 2. Upload Ebook File
        const ebookFileName = `${Date.now()}-${ebookFile.name}`;
        const { error: ebookUploadError } = await supabase.storage
            .from('ebook-files')
            .upload(ebookFileName, ebookFile);

        if (ebookUploadError) throw ebookUploadError;
        
        const { data: { publicUrl: ebookFileUrl } } = supabase.storage
            .from('ebook-files')
            .getPublicUrl(ebookFileName);
        
        // 3. Insert into database
        const { error: dbError } = await supabase.from('ebooks').insert({
          title,
          author,
          price,
          description,
          coverImageId: coverImageUrl, // Using coverImageId to store the URL now
          ebook_file_path: ebookFileUrl,
        });

        if (dbError) throw dbError;

        toast({
            title: "Ebook Added",
            description: `"${title}" has been successfully added.`,
        });
        router.push("/admin");

    } catch (error: any) {
        toast({
            title: "Error adding ebook",
            description: error.message,
            variant: "destructive",
        });
    } finally {
        setIsLoading(false);
    }
  };
  
  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setCoverImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleEbookFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
        setEbookFile(file);
    }
  };


  return (
    <div className="mx-auto grid max-w-[59rem] flex-1 auto-rows-max gap-4">
      <div className="flex items-center gap-4">
        <Button variant="outline" size="icon" className="h-7 w-7" asChild>
          <Link href="/admin">
            <ChevronLeft className="h-4 w-4" />
            <span className="sr-only">Back</span>
          </Link>
        </Button>
        <h1 className="flex-1 shrink-0 whitespace-nowrap text-xl font-semibold tracking-tight sm:grow-0">
          Add New eBook
        </h1>
        <Badge variant="outline" className="ml-auto sm:ml-0">
          New
        </Badge>
        <div className="hidden items-center gap-2 md:ml-auto md:flex">
          <Button variant="outline" size="sm" asChild>
            <Link href="/admin">Discard</Link>
          </Button>
          <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Save eBook
          </Button>
        </div>
      </div>
      <form
        onSubmit={handleSubmit}
        className="grid gap-4 md:grid-cols-[1fr_250px] lg:grid-cols-3 lg:gap-8"
      >
        <div className="grid auto-rows-max items-start gap-4 lg:col-span-2 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Ebook Details</CardTitle>
              <CardDescription>
                Fill in the details for the new ebook.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid gap-6">
                <div className="grid gap-3">
                  <Label htmlFor="title">Title</Label>
                  <Input
                    id="title"
                    type="text"
                    className="w-full"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder="The Art of Hacking"
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="author">Author</Label>
                  <Input
                    id="author"
                    type="text"
                    className="w-full"
                    value={author}
                    onChange={(e) => setAuthor(e.target.value)}
                    placeholder="John Doe"
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="min-h-32"
                    placeholder="A book about..."
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="price">Price (GHâ‚µ)</Label>
                  <Input
                    id="price"
                    type="number"
                    value={price}
                    onChange={(e) => setPrice(parseFloat(e.target.value) || 0)}
                  />
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
        <div className="grid auto-rows-max items-start gap-4 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Cover Image</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-2">
                <div className="relative aspect-[2/3] w-full rounded-md overflow-hidden border flex items-center justify-center bg-muted">
                  {imagePreview ? (
                    <Image
                      alt="Product image"
                      className="object-cover"
                      fill
                      src={imagePreview}
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                    />
                  ) : (
                    <div className="text-center text-muted-foreground">
                      <ImageIcon className="h-8 w-8 mx-auto" />
                      <p className="mt-2 text-sm">No image</p>
                    </div>
                  )}
                </div>
                <Input
                  id="cover-image-upload"
                  type="file"
                  className="hidden"
                  ref={coverFileInputRef}
                  onChange={handleImageUpload}
                  accept="image/*"
                />
                <Button type="button" variant="outline" className="w-full" onClick={() => coverFileInputRef.current?.click()}>
                  <Upload className="h-4 w-4" />
                  <span className="ml-2">Upload Image</span>
                </Button>
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
                <CardTitle>eBook File</CardTitle>
                <CardDescription>Upload the PDF file for the ebook.</CardDescription>
            </CardHeader>
            <CardContent>
                <Input 
                    id="ebook-file-upload"
                    type="file"
                    className="hidden"
                    ref={ebookFileInputRef}
                    onChange={handleEbookFileUpload}
                    accept=".pdf"
                />
                <Button type="button" variant="outline" className="w-full" onClick={() => ebookFileInputRef.current?.click()}>
                    <Upload className="h-4 w-4" />
                    <span className="ml-2">Upload PDF</span>
                </Button>
                {ebookFile && (
                    <div className="mt-4 flex items-center gap-2 text-sm text-muted-foreground">
                        <FileText className="h-4 w-4" />
                        <span>{ebookFile.name}</span>
                    </div>
                )}
            </CardContent>
          </Card>
        </div>
      </form>
      <div className="flex items-center justify-center gap-2 md:hidden">
        <Button variant="outline" size="sm" asChild>
            <Link href="/admin">Discard</Link>
        </Button>
        <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
          {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
          Save eBook
        </Button>
      </div>
    </div>
  );
}
